*, *::before, *::after, *::before {
    box-sizing: border-box;
}

body {
    background-color:#F3f3f3;
    margin: 0;
}
.container .ql-editor{
    width: 8.5in;
    min-height: 11in;
    padding:1in;
    margin: 1rem ;
    box-shadow: 0 0 5px 0  rgb(58, 56, 56);
    background-color: white;
}

.container .ql-container.ql-snow{
    border: none;
    display:flex;
    justify-content:center;
}

.container .ql-toolbar.ql-snow{
    display:flex;
    flex-wrap: wrap;
    justify-content:center;
    position:sticky;
    top: 0;
    z-index:1 ;
    background-color: #fefefe;
    border: none;
    box-shadow: 0 0 5px 0 rgb(58, 56, 56);
}
@media (max-width:450px) {
.container .ql-toolbar.ql-snow{
    display:inline-flex;
}
}
@page{
    /* it dictates the core things */
    margin: 1in;

}
@media print {
    body {
        background: none;
    }
    .container .ql-editor{
        width: 6.5;
        height: 9in;
        padding: 0;
        margin: 0;  
        box-shadow: none;
        align-self: flex-start;    
    }
    .container .ql-toolbar.ql-snow{
        display:none;
    }
}